import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';

import {
  ModalOverlay,
  ModModalOverlay,
  ModalContent,
  FormGroup,
  Label,
  Input,
  Button
} from './ModalEntityEdit.style';
import EntityAPI from '../../../../../utils/apiEntity';

const ModalEntityEdit = ({ showModal, setShowModal, type, data, setData, setLoadedEntityDetails }) => {
  const [formData, setFormData] = useState({});

  useEffect(() => {
    setFormData(data);  //   
    console.log(data);
  }, [data]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async () => {
    const response = await EntityAPI.editEntity(formData);
    alert('  !');
    setShowModal(false);
    setData(formData);
    setLoadedEntityDetails(formData);
  };

  return ReactDOM.createPortal(
    <ModalOverlay onClick={() => setShowModal(false)}>
      <ModalContent onClick={e => e.stopPropagation()}>
        <h2> {type === 'contact' ? '' : ''}</h2>
        {type === 'contact' && (
          <>
            <FormGroup>
              <Label></Label>
              <Input
                name="phone"
                placeholder=""
                value={formData.phone || ''}
                onChange={handleChange}
                readOnly
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Input
                name="name"
                placeholder=""
                value={formData.name || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Input
                name="series"
                placeholder=""
                value={formData.series || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Input
                name="number"
                placeholder=""
                value={formData.number || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label> </Label>
              <Input
                name="issueDate"
                type="date"
                placeholder=" "
                value={formData.issueDate || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label> </Label>
              <Input
                name="issuedBy"
                placeholder=" "
                value={formData.issuedBy || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Input
                name="code"
                placeholder=""
                value={formData.code || ''}
                onChange={handleChange}
              />
            </FormGroup>
          </>
        )}
        {type === 'company' && (
          <>
            <FormGroup>
              <Label></Label>
              <Input
                name="inn"
                placeholder=""
                value={formData.inn || ''}
                onChange={handleChange}
                readOnly
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Input
                name="name"
                placeholder=""
                value={formData.name || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Input
                name="address"
                placeholder=""
                value={formData.address || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label> </Label>
              <Input
                name="contactPerson"
                placeholder=" "
                value={formData.contactPerson || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label></Label>
              <Input
                name="position"
                placeholder=""
                value={formData.position || ''}
                onChange={handleChange}
              />
            </FormGroup>
            <FormGroup>
              <Label> </Label>
              <Input
                name="company_phone"
                placeholder=" "
                value={formData.company_phone || ''}
                onChange={handleChange}
              />
            </FormGroup>
          </>
        )}
        <Button onClick={handleSubmit}></Button>
      </ModalContent>
    </ModalOverlay>,
    document.body
  );
};

export default ModalEntityEdit;